import React from "react";
import { Center, Flex, Image, SimpleGrid, Stack, Text, VStack } from "@chakra-ui/react";
import { Tooltip } from "@mantine/core";
import { Icon } from "@iconify/react";
export default function Experience() {
  return (
    <VStack>
      <SimpleGrid gap={40} alignItems={"center"} columns={[1, 1, 2, 2]} w={["80%", "80%", "80%", "50%"]} m="auto" mt={10} mb={10}>
        <Image w={500} src={require("../images/CareerProgress.png")} />
        <Flex flexDirection={"column"} alignItems={"center"}>
          <Text fontSize={48}>Experience</Text>
          <Text fontSize={22}>Full-Time and Part-Time</Text>
          <Flex alignItems={"center"} justifyContent={"center"} w={"40px"} mt={"10px"}>
            <Tooltip label="Github" color="dark" withArrow>
              <Icon
                onClick={() => {
                  window.open("https://github.com/meshiv5", "_blank");
                }}
                className="icons"
                icon="grommet-icons:github"
                color="orange"
                width={25}
              />
            </Tooltip>
          </Flex>
        </Flex>
      </SimpleGrid>
      <Flex>
        <Center fontSize={"34px"}>Employment</Center>
      </Flex>
    </VStack>
  );
}
