import "../styles/Navbar.css";
import React, { useContext, useEffect, useState } from "react";
import { Flex, Text } from "@chakra-ui/react";
import { Navigate, NavLink, useLocation, useNavigate } from "react-router-dom";
import LightDarkButton from "./LightDarkButton";
import { ThemeContext } from "../Context/ThemeContext";
import MenuButton from "./Menubutton";

export default function Navbar() {
  const { colorScheme } = useContext(ThemeContext);
  const [opened, setOpened] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();
  const styles = {
    backgroundColor: "rgba(24, 100, 171, 0.25)",
    color: " rgb(116, 192, 252)",
  };
  useEffect(() => {
    if (opened == true) setOpened(false);
  }, [location]);
  return (
    <Flex mt={4} alignItems={"center"} justifyContent={["space-around", "space-around", "space-around", "center"]}>
      <Flex alignItems={"center"}>
        <Text fontSize={[20, 26, 26, 30, 32]} mr={4} className="fontLato ArrowName">
          {"<"}
        </Text>
        <Text
          fontSize={[22, 32, 20, 32, 36]}
          bgGradient="linear-gradient(45deg, rgb(121, 80, 242) 0%, rgb(76, 110, 245) 100%);"
          padding={2}
          bgClip="text"
          className="fontAgust siteTitle"
          onClick={() => {
            navigate("/");
          }}
        >
          Shivendra Upadhyay
        </Text>
        <Text fontSize={[20, 26, 26, 30, 32]} ml={4} className="fontLato ArrowName">
          {"/>"}
        </Text>
      </Flex>
      <Flex display={["none", "none", "flex", "flex"]} ml={58} justifyContent={"space-between"} w={400} className="fontLato">
        <NavLink
          style={({ isActive }) => (isActive && colorScheme === "dark" ? styles : {})}
          end
          className={({ isActive }) => (isActive ? "linkActive" : "linkInActive")}
          to={"/"}
        >
          Home
        </NavLink>
        <NavLink
          style={({ isActive }) => (isActive && colorScheme === "dark" ? styles : {})}
          className={({ isActive }) => (isActive ? "linkActive" : "linkInActive")}
          to={"/education"}
        >
          Education
        </NavLink>
        <NavLink
          style={({ isActive }) => (isActive && colorScheme === "dark" ? styles : {})}
          className={({ isActive }) => (isActive ? "linkActive" : "linkInActive")}
          to={"/experience"}
        >
          Experience
        </NavLink>
        <NavLink
          style={({ isActive }) => (isActive && colorScheme === "dark" ? styles : {})}
          className={({ isActive }) => (isActive ? "linkActive" : "linkInActive")}
          to={"/projects"}
        >
          Projects
        </NavLink>
        <NavLink
          style={({ isActive }) => (isActive && colorScheme === "dark" ? styles : {})}
          className={({ isActive }) => (isActive ? "linkActive" : "linkInActive")}
          to={"/contact"}
        >
          Contact
        </NavLink>
      </Flex>
      <Flex ml={[10, 10, 20]} alignItems={"center"}>
        <LightDarkButton />
      </Flex>
      <Flex flexDirection={"column"} display={["flex", "flex", "none", "none"]}>
        <MenuButton opened={opened} setOpened={setOpened} />
      </Flex>
      <Flex
        className={colorScheme === "dark" ? "menuDark" : "menuLight"}
        bg={colorScheme === "dark" ? "#1A1B1E" : "white"}
        display={[opened ? "flex" : "none", opened ? "flex" : "none", "none", "none"]}
      >
        <NavLink
          style={({ isActive }) => (isActive && colorScheme === "dark" ? styles : {})}
          end
          className={({ isActive }) => (isActive ? "linkActive" : "linkInActive")}
          to={"/"}
        >
          Home
        </NavLink>
        <NavLink
          style={({ isActive }) => (isActive && colorScheme === "dark" ? styles : {})}
          className={({ isActive }) => (isActive ? "linkActive" : "linkInActive")}
          to={"/education"}
        >
          Education
        </NavLink>
        <NavLink
          style={({ isActive }) => (isActive && colorScheme === "dark" ? styles : {})}
          className={({ isActive }) => (isActive ? "linkActive" : "linkInActive")}
          to={"/experience"}
        >
          Experience
        </NavLink>
        <NavLink
          style={({ isActive }) => (isActive && colorScheme === "dark" ? styles : {})}
          className={({ isActive }) => (isActive ? "linkActive" : "linkInActive")}
          to={"/projects"}
        >
          Projects
        </NavLink>
        <NavLink
          style={({ isActive }) => (isActive && colorScheme === "dark" ? styles : {})}
          className={({ isActive }) => (isActive ? "linkActive" : "linkInActive")}
          to={"/contact"}
        >
          Contact
        </NavLink>
      </Flex>
    </Flex>
  );
}
